    margin: 0;
    padding: 0;
}

body {
    height: 100vh;
    display: grid;
    grid-template-rows: 1fr 8fr;
}

#content {
    font-family: "Montserrat", sans-serif;
    color: #424d3c;
    grid-row: 2;
    display: grid;
    grid-template-rows: 7fr auto;
}

header {
    grid-row: 1;
    font-size: 25px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: #7a9448;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-around;
    white-space: nowrap;
    flex-wrap: wrap;
    gap: 15px;
    font-weight: 600;
    letter-spacing: 1em;
    padding: 1em 0em 1em 0em;
}

button {
    margin: 15px;
    background-color: #7a9448;
    color: white;
    font-weight: 600;
    border: none;
    min-width: 100px;
    padding: 10px 20px;
    border-radius: 5px;  
}

.active {
    background-color: #bed396;
}

footer {
    font-size: 18px;
    padding: 1rem;
    text-align: center;
}

/* home */
.page1-container{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    display: grid;
    background-position: center;
    background-size: cover;
}

.page1-text {
    background-color: rgba(255, 255, 255, 0.75);
    padding: 4rem;
    width: 23vw;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-left: 9rem;
}
  
  .page1-heading {
    font-size: 25px;
    font-weight: 500;
}
  
  .page1-paragraph {
    font-size: 20px;
    line-height: 2rem;
}

/* menu */
.page2-container {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-position: center;
    background-size: cover;
    display: flex;
    justify-content: center;
}

.container-text {
    background-color: rgba(255, 255, 255, 0.8);
    width: 30vw;
    padding: 3rem;
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: 2rem;
}

.page2-text {
    font-weight: 500;
    font-size: 18px;
    line-height: 2rem;
}

.menu-title {
    font-weight: 600;
    font-size: 20px;
}

.menu-ingredients {
    font-size: 17px;
    line-height: 2rem;
}

/* contact */
.page3-container {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    background-position: center;
    background-size: cover;
    display: flex;
    justify-content: end;
}

.contact-text {
    background-color: rgba(255, 255, 255, 0.8);
    padding: 3rem 4rem 3rem 4rem;
    width: 23vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 1rem;
    margin-right: 9rem;
}

.iframe {
    width: 100%;
    height: 300px;
    border: none;   
}

.contact-paragraph {
    font-size: 15px;
    line-height: 2rem;
    font-weight: 600;
}

